// ============================================
// 全局样式 - 只包含真正的全局重置和基础样式
// ============================================

@import './variables.scss';
@import './mixins.scss';

// ============================================
// 全局重置
// ============================================
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $text-primary;
  background-color: $bg-primary;
  line-height: 1.6;
}

// ============================================
// 基础元素样式
// ============================================

// 链接
a {
  color: $primary-color;
  text-decoration: none;
  transition: color $transition-fast;
  
  &:hover {
    color: $primary-dark;
  }
}

// 标题
h1, h2, h3, h4, h5, h6 {
  margin-bottom: $spacing-md;
  font-weight: 600;
  line-height: 1.2;
}

h1 { font-size: 2.5rem; }
h2 { font-size: 2rem; }
h3 { font-size: 1.75rem; }
h4 { font-size: 1.5rem; }
h5 { font-size: 1.25rem; }
h6 { font-size: 1rem; }

// 段落
p {
  margin-bottom: $spacing-md;
}

// 按钮
button {
  font-family: inherit;
  outline: none;
  
  &:focus-visible {
    outline: 2px solid $primary-color;
    outline-offset: 2px;
  }
}

// 输入框
input, textarea {
  font-family: inherit;
  
  &:focus {
    outline: none;
  }
}

// 列表
ul, ol {
  margin-bottom: $spacing-md;
  padding-left: $spacing-lg;
}

// 代码
code {
  font-family: 'Courier New', Courier, monospace;
  background-color: rgba($primary-color, 0.1);
  padding: 2px 6px;
  border-radius: $radius-sm;
  font-size: 0.9em;
}

// ============================================
// 通用工具类
// ============================================

// 容器
.container {
  max-width: $max-width;
  margin: 0 auto;
  padding: 0 $spacing-lg;
}

.container-narrow {
  max-width: $max-width-narrow;
  margin: 0 auto;
  padding: 0 $spacing-lg;
}

// 文本对齐
.text-center { text-align: center; }
.text-left { text-align: left; }
.text-right { text-align: right; }

// Flex 工具类
.flex { display: flex; }
.flex-center { @include flex-center; }
.flex-between { @include flex-between; }
.flex-column { @include flex-column; }

// 间距工具类
.mt-sm { margin-top: $spacing-sm; }
.mt-md { margin-top: $spacing-md; }
.mt-lg { margin-top: $spacing-lg; }

.mb-sm { margin-bottom: $spacing-sm; }
.mb-md { margin-bottom: $spacing-md; }
.mb-lg { margin-bottom: $spacing-lg; }

.pt-sm { padding-top: $spacing-sm; }
.pt-md { padding-top: $spacing-md; }
.pt-lg { padding-top: $spacing-lg; }

.pb-sm { padding-bottom: $spacing-sm; }
.pb-md { padding-bottom: $spacing-md; }
.pb-lg { padding-bottom: $spacing-lg; }

// Flex 增长
.grow-1 { flex-grow: 1; }
