// ============================================
// Home 页面特有样式
// ============================================
// 说明：此文件只包含 Home 页面特有的样式
// 全局样式和共享组件样式已在 src/index.tsx 中通过 @/styles/index.scss 导入
// ============================================

@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

// 创建新机器人按钮的图标容器
.tm-row-add {
  .icon-container {
    width: 34px;
    height: 34px;
    min-width: 34px;
    min-height: 34px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
  }
}

// 搜索区域
.tm-main-search-form {
  padding: 0 16px;
  margin: 0;
}

// ============================================
// 机器人列表特有样式
// ============================================

// 机器人头像
.tm-row-pic {
  width: 34px;
  height: 34px;
  min-width: 34px;
  min-height: 34px;
  max-width: 34px;
  max-height: 34px;
  border-radius: 4px;
  flex-shrink: 0;
  object-fit: cover;
  background-color: var(--text-color);
}

.tm-row-pic-user {
  border-radius: 100px;
}

// 机器人信息
.tm-row-value {
  color: var(--header-color);
  font-weight: 600;
  font-size: 14px;
  text-overflow: ellipsis;
  overflow: clip;
}

.tm-row-description {
  font-size: 12px;
  text-overflow: ellipsis;
  overflow: clip;
  color: var(--text-color);
}

// 空结果容器
.tm-row-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba(var(--header-bg-rgb), 1);
  border-radius: 6px 6px 0 0;
  padding: 18px 18px 12px 18px;
  margin-bottom: 1px;
}

.tm-row-results-empty {
  min-height: 240px;
  justify-content: center;
  text-align: center;

  svg,
  img {
    opacity: 0.325;
    margin-bottom: 12px;
  }

  b {
    font-size: 17px;
    font-weight: 600;
    color: var(--header-color);
    margin-bottom: 8px;
    display: block;
  }

  .js-results-empty-help {
    font-size: 14px;
    color: var(--text-color);
    max-width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
  }
}
