// ============================================
// BotDetail 页面特有样式
// ============================================
// 说明：此文件只包含 BotDetail 页面特有的样式
// 全局样式和共享组件样式已在 src/index.tsx 中通过 @/styles/index.scss 导入
// ============================================

@import '@/styles/variables.scss';
@import '@/styles/mixins.scss';

// ============================================
// 局部复刻 Bot Token 卡片样式（不依赖 botfather11.css）
// 作用域：仅在该详情页中使用到的类名
// ============================================

.tm-section {
  .tm-api-token-wrap {
    width: 100%;
    padding: 14px;
    background: $bg-secondary; // 浅色背景
    border: none; // 去描边
    border-radius: 6px;
  }

  .tm-api-token {
    display: flex;
    align-items: center;
    background: $bg-primary; // 内部使用白底，增强层次
    padding: 12px;
    border-radius: 6px;
    margin-bottom: 8px;
    font-size: 14px;
    color: $text-primary;
    word-break: break-all;
  }

  .tm-token-text {
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .tm-api-token-actions {
    display: flex;
    justify-content: stretch;
    gap: 10px;
  }

  .tm-active-button,
  .tm-revoke-button {
    flex: 1 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px 20px;
    border-radius: 6px;
    font-weight: 600;
    font-size: 14px;
    cursor: pointer;
    user-select: none;
  }

  .tm-active-button {
    background: #1890ff; // 参考项目主色
    color: #fff;

    &:hover { background: #0076e4; }
    &:active { transform: translateY(0.5px); }
  }

  .tm-revoke-button {
    background: #ff4d4f; // 危险操作色
    color: #fff;

    &:hover { background: #d93436; }
    &:active { transform: translateY(0.5px); }
  }

  // 文案辅助样式（局部）
  // .help-text 已在 components.scss 中定义，这里只做特殊覆盖
  .help-text.link-chevrons {
    line-height: 19px;
  }
}

// ============================================
// BotDetail 特有组件样式
// ============================================
// 注意：.tm-menu-items 和 .tm-icon 已移到 components.scss

// 危险操作样式
.tm-row-destructive {
  color: #F88277 !important;
  
  &:hover {
    background: rgba(#F88277, 0.1) !important;
  }
}

// 用户名链接
.tm-username-link {
  &.js-usernames {
    cursor: pointer;
    
    &:hover {
      text-decoration: underline;
    }
  }
}
