// ============================================
// 共享组件样式
// 说明：这里只包含被多个页面共享使用的组件样式
// ============================================

@import './variables.scss';

// ============================================
// CSS 变量定义
// ============================================
:root {
  // 颜色变量
  --text-color: #{$text-secondary};
  --accent-color: #{$primary-color};
  --accent-rgb: #{$primary-rgb};
  --header-color: #{$text-header};
  --field-color: #{$text-primary};
  --field-bg-color: #{$bg-secondary};
  --field-placeholder-color: #{$text-disabled};
  
  // 按钮颜色
  --btn-primary-color: #fff;
  --btn-primary-bg-color: #{$primary-color};
  --btn-primary-bg-hover-color: #{$primary-dark};
  
  // 背景颜色
  --bg-color: #{$bg-primary};
  --header-bg-rgb: 245, 245, 245;
  --table-bg-hover-color: #{$bg-table-hover};
  
  // 通用配置
  --def-border-radius: #{$radius-lg};
  --def-transition: #{$transition-fast};
}

// ============================================
// 主容器（所有页面共享）
// ============================================
#aj_content {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: var(--bg-color);
}

.tm-main {
  margin: 0 auto;
  width: 480px;
  max-width: 100%;
  padding-bottom: 12px;
  flex-grow: 1;
}

// ============================================
// 介绍区域（Home, BotDetail, CommandDetail, CreateBot 共享）
// ============================================
.tm-main-intro {
  text-align: center;
  padding: 21px 12px;

  .tm-main-intro-picture {
    width: 64px;
    height: 64px;
    border-radius: 50%;
    object-fit: cover;
  }

  .tm-main-intro-header {
    font-size: 22px;
    line-height: 30px;
    font-weight: bold;
    color: var(--header-color);
    margin: 0;
    overflow: clip;
    text-overflow: ellipsis;
  }

  .tm-main-intro-text, .help-text {
    font-size: 13px;
    line-height: 17px;
    text-wrap: pretty;
    margin: 4px auto;
    color: var(--text-color);

   
  }
}

.tm-main-intro-text, .help-text {

  a {
    color: var(--accent-color);
    text-decoration: none;
    display: inline-flex;
    align-items: baseline;
    gap: 4px;
    
    &::after {
      content: '';
      display: inline-block;
      width: 12px;
      height: 12px;
      background-image: url("data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 12 12' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 2l4 4-4 4' fill='none' stroke='%23248BDA' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      transition: transform 0.2s ease;
      position: relative;
      top: 2px;
    }
    
    &:hover {
      text-decoration: underline;
      
      &::after {
        transform: translateX(2px);
      }
    }
  }
}

// ============================================
// 区域容器（所有页面共享）
// ============================================
.tm-section {
  padding: 0 16px 12px;

  .tm-section-header {
    display: flex;
    align-items: center;
    padding: 9px 12px;
    width: 100%;
    gap: 10px;

    .tm-section-header-text {
      font-size: 18px;
      line-height: 28px;
      font-weight: bold;
      color: var(--header-color);
      margin: 0;
      padding: 0;
      display: flex;
    }
  }
}

// ============================================
// 表单字段（CreateBot, CommandEdit 共享）
// ============================================
.tm-field {
  border-radius: var(--def-border-radius);
  background: var(--field-bg-color);
  position: relative;
  display: flex;
  transition: all 0.2s ease;

  &:focus-within {
    box-shadow: 0 0 0 2px rgba(36, 139, 218, 0.15);
    background-color: rgba(36, 139, 218, 0.05);
  }
}

// 输入框
.form-control {
  height: auto;
  font-size: 14px;
  line-height: 20px;
  padding: 11px 14px;
  word-break: break-word;
  box-shadow: none !important;
  border: none;
  background: var(--field-bg-color) !important;
  border-radius: var(--def-border-radius);
  color: var(--field-color);
  width: 100%;

  &:focus {
    outline: none;
  }

  &::placeholder {
    color: var(--field-placeholder-color);
    transition: opacity 0.15s ease;
  }

  &:focus::placeholder {
    opacity: 0.4;
  }
}

.tm-input {
  font-size: 14px;
  background: transparent;
}

// Textarea 自适应
textarea.tm-input {
  resize: none;
  overflow: hidden;
  transition: height 0.2s ease;
  min-height: 42px;
}

// 固定前缀
.tm-field-const-prefix {
  padding: 12px 14px;
  padding-right: 0;
  color: $text-secondary;
  font-size: 14px;
  line-height: 18px;
  white-space: nowrap;
}

.tm-field-const-prefix + .tm-input {
  padding-left: 0;
}

// ============================================
// 帮助文本（多个页面共享）
// ============================================
.help-text {
  margin: 8px 14px;
  font-size: 13px;
  color: var(--text-color);
  line-height: 1.5;

  code {
    background-color: rgba($primary-color, 0.1);
    padding: 2px 6px;
    border-radius: 3px;
    font-family: 'Monaco', 'Menlo', monospace;
    font-size: 0.95em;
    color: $text-primary;
  }
}

// ============================================
// 按钮样式（多个页面共享）
// ============================================
.btn {
  display: inline-flex;
  font-size: 13px;
  line-height: 18px;
  font-weight: bold;
  padding: 12px 15px;
  justify-content: center;
  border-radius: var(--def-border-radius);
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;

  &:focus,
  &:active,
  &:focus:active {
    box-shadow: none;
    outline: none;
  }
}

.btn-primary {
  color: var(--btn-primary-color);
  background-color: var(--btn-primary-bg-color);

  &:hover:not(:disabled) {
    color: var(--btn-primary-color);
    background-color: var(--btn-primary-bg-hover-color);
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(36, 139, 218, 0.3);
  }

  &:active:not(:disabled) {
    color: var(--btn-primary-color);
    background-color: var(--btn-primary-bg-hover-color);
    transform: translateY(0);
    box-shadow: 0 2px 6px rgba(36, 139, 218, 0.2);
  }

  &:disabled {
    color: var(--btn-primary-color);
    background-color: var(--btn-primary-bg-color);
    opacity: 0.6;
    cursor: not-allowed;
  }
}

// ============================================
// 表格包装器（所有页面共享）
// ============================================
.tm-table-wrap {
  border-radius: var(--def-border-radius);
  margin-bottom: 0;
  border: none;
  overflow: hidden;
  width: 100%;

  &.hidden {
    display: none;
  }
}

// ============================================
// 行容器（所有页面共享）
// ============================================
.tm-row {
  display: flex;
  width: 100%;
  padding: 9px 14px;
  padding-right: 24px;
  background: rgba(var(--header-bg-rgb), 1);
  margin-bottom: 1px;
  position: relative;
  gap: 8px;
  cursor: pointer;
  min-height: 42px;
  align-items: center;
  font-size: 14px;
  color: $text-primary;
  text-decoration: none;

  &:hover {
    background: var(--table-bg-hover-color);
  }

  > div {
    min-width: 0;
  }
}

// 行箭头（多个页面的 tm-row-link 共享）
.tm-row-link:after {
  content: '';
  display: inline-block;
  vertical-align: top;
  background-position: 0 center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 9px;
  height: 17px;
  background-image: url("data:image/svg+xml,%3Csvg height='17' viewBox='0 0 11 17' width='11' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='m3 14 5.15-5.11c.19-.2.19-.52 0-.71l-5.15-5.18' fill='none' stroke='%23999999' stroke-linecap='round' stroke-width='2'/%3E%3C/svg%3E");
  position: absolute;
  top: 0;
  right: 12px;
  bottom: 0;
  margin: auto 0;
}

// 添加按钮行（多个页面共享）
.tm-row-add {
  font-size: 14px;
  display: flex;
  color: var(--accent-color) !important;

  &:after {
    content: none;
  }
}

// ============================================
// 图标样式（BotDetail, CommandDetail 共享）
// ============================================
.tm-icon {
  width: 24px;
  height: 24px;
  background: currentColor;
  display: inline-block;
  flex-shrink: 0;
  mask-size: 100%;
  -webkit-mask-size: 100%;
  mask-repeat: no-repeat;
  -webkit-mask-repeat: no-repeat;
  mask-position: center;
  -webkit-mask-position: center;
  mask-image: var(--icon-s);
  -webkit-mask-image: var(--icon-s);
}

// ============================================
// 菜单项容器（BotDetail, CommandDetail 共享）
// ============================================
.tm-menu-items {
  // 用于包含一组菜单项的容器
  display: block;
}

// ============================================
// Bot Anchor（CommandDetail, CommandEdit 共享）
// ============================================
.tm-bot-anchor {
  &.tr-bot-anchor {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px 16px;
    cursor: pointer;
    transition: background-color 0.2s ease;

    &:hover {
      background-color: $bg-hover;
    }

    .tr-bot-avatar {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      object-fit: cover;
    }

    .tr-bot-name {
      font-size: 14px;
      font-weight: 600;
      color: $text-primary;
    }
  }
}

// ============================================
// 响应式设计
// ============================================
@media (max-width: 768px) {
  .tm-main {
    width: 100%;
    padding-bottom: 24px;
  }

  .tm-section {
    padding: 0 12px 12px;
  }

  .tm-main-intro {
    padding-left: 12px;
    padding-right: 12px;
  }

  .btn {
    font-size: 15px;
    line-height: 18px;
    padding: 14px 20px;
  }
}

// 触摸设备优化
@media (hover: none) {
  .btn-primary {
    &:hover {
      transform: none;
    }
  }
}

// 暗黑模式优化
@media (prefers-color-scheme: dark) {
  .tm-field {
    &:focus-within {
      box-shadow: 0 0 0 2px rgba(36, 139, 218, 0.2);
    }
  }
}

