// ============================================
// CreateBot 页面特有样式
// ============================================
// 说明：此文件只包含 CreateBot 页面特有的样式
// 全局样式和共享组件样式已在 src/index.tsx 中通过 @/styles/index.scss 导入
// ============================================

@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

// 固定在底部的提交按钮
.fixed-bottom-button {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 12px 16px;
  background: #fff;
  box-shadow: 0 -2px 8px rgba(0, 0, 0, 0.08);
  z-index: 100;
  
  // 安全区域适配（适配 iPhone 刘海屏等）
  padding-bottom: calc(12px + env(safe-area-inset-bottom));
}

// 头像上传交互效果
.tm-main-intro-picture {
  cursor: pointer;
  transition: transform 0.2s ease, opacity 0.2s ease;
  
  &:hover {
    transform: scale(1.05);
    opacity: 0.9;
  }
  
  &:active {
    transform: scale(0.98);
  }
}

// ============================================
// CreateBot 特有组件样式
// ============================================

// 清除按钮和加载动画的通用样式
.icon-before {
  display: inline-block;
  vertical-align: top;
  background-position: 0 center;
  background-repeat: no-repeat;
  background-size: contain;
}

.icon-search-clear::before {
  content: '';
  width: 18px;
  height: 18px;
  background-image: url('data:image/svg+xml,%3Csvg%20height%3D%2222%22%20viewBox%3D%220%200%2022%2022%22%20width%3D%2222%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22m5%2017%2012-12m-12%200%2012%2012%22%20stroke%3D%22%23999999%22%20stroke-linecap%3D%22round%22%20stroke-width%3D%222%22%2F%3E%3C%2Fsvg%3E');
}

.tm-search-clear {
  position: absolute;
  top: 0;
  right: 0;
  padding: 12px;
  cursor: pointer;
  border-radius: var(--def-border-radius);
  transition: visibility var(--def-transition), opacity var(--def-transition);

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.5;
    transform: scale(0.9);
  }
}

.tm-input:placeholder-shown ~ .tm-search-clear {
  visibility: hidden;
  opacity: 0;
}

// 加载动画
.tm-search-loading {
  position: absolute;
  top: 0;
  right: 0;
  padding: 12px;
  visibility: hidden;
  opacity: 0;
  transition: visibility var(--def-transition), opacity var(--def-transition);

  .circle-progress-wrap {
    transform-origin: 50%;
    vertical-align: top;
    width: 22px;
    height: 22px;
  }

  .circle-progress {
    stroke: $text-disabled;
    stroke-width: 2px;
    stroke-linecap: round;
    fill: transparent;
    transform: rotateZ(-90deg);
    transform-origin: 50%;
    stroke-dasharray: 62px;
    r: 10px;

    &.play {
      animation: rotate-circle linear 1.5s infinite,
                 resize-circle linear 360s infinite;
    }
  }
}

@keyframes rotate-circle {
  from { transform: rotateZ(-90deg); }
  to   { transform: rotateZ(270deg); }
}

@keyframes resize-circle {
  from { stroke-dashoffset: 62px; }
  to   { stroke-dashoffset: 6262px; }
}

// 提示文本
.hint-text {
  margin: 8px 14px;
  font-size: 13px;
  color: var(--text-color);
  display: none;

  &.hint-text-error {
    display: block;
    color: $error-color;
    animation: shake 0.3s ease;
  }
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  75% { transform: translateX(5px); }
}