// ============================================
// CommandEdit 页面特有样式
// ============================================
// 说明：此文件只包含 CommandEdit 页面特有的样式
// 全局样式和共享组件样式已在 src/index.tsx 中通过 @/styles/index.scss 导入
// ============================================

@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

// ============================================
// CommandEdit 页面特有样式
// ============================================
// 说明：此页面使用自定义配色方案
// ============================================

// 页面容器
.tm-command-edit-page {
  #aj_content {
    padding-top: 12px;
  }

  .tm-main {
    padding-bottom: 80px;
  }

  // 区域头部特殊样式
  .tm-section-header-text {
    color: #111827;
  }

  // 输入框区域特殊样式
  .tm-field {
    background: #f5f7fa;

    .tm-field-const-prefix {
      color: #111827;
      font-weight: 500;
    }

    .form-control {
      color: #111827;

      &::placeholder {
        color: #9ca3af;
      }
    }
  }
}

// 表单操作按钮
.tm-form-actions {
  display: flex;
  gap: 12px;
  margin-top: 16px;
  padding: 0 12px;

  .tm-btn {
    flex: 1;
    display: inline-flex;
    font-size: 13px;
    line-height: 18px;
    font-weight: bold;
    padding: 12px 15px;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    border: none;
    cursor: pointer;
    transition: all 0.2s ease;
    outline: none;

    &:hover {
      transform: translateY(-1px);
    }

    &:active {
      transform: translateY(0);
    }

    &.tm-btn-primary {
      color: #fff;
      background-color: #248bda;

      &:hover {
        background-color: #207cc2;
      }

      &:disabled {
        background-color: #93c5ed;
        cursor: not-allowed;
        opacity: 0.6;
        transform: none;
      }
    }

    &.tm-btn-secondary {
      color: #111827;
      background-color: #e5e7eb;

      &:hover {
        background-color: #d1d5db;
      }
    }
  }
}

// CommandEdit 特有的行样式
.tm-command-edit-page {
  .tm-row {
    background: #f5f7fa;
    color: #111827;

    &:hover {
      background: #e5e7eb;
    }

    &.tm-row-toggle {
      padding-right: 9px;

      &:active {
        background: #e5e7eb;
      }
    }

    span {
      font-weight: normal;
    }
  }
}

// Toggle 开关样式
.tm-toggle {
  height: 20px;
  width: 36px;
  border-radius: 100px;
  background: #e3695d;
  padding: 2px;
  flex-shrink: 0;
  transition: background 0.2s ease-in;
  position: relative;

  .tm-toggle-handle {
    width: 16px;
    height: 16px;
    background: #fff;
    border-radius: 100px;
    position: relative;
    left: 0;
    transition: left 0.3s cubic-bezier(0.23, 1.0, 0.32, 1.0);
  }

  &.tm-toggle-on {
    background: #248bda;

    .tm-toggle-handle {
      left: 16px;
    }
  }
}

// CommandEdit 特有的帮助文本样式
.tm-command-edit-page {
  .help-text {
    color: #6b7280;

    a {
      color: #248bda;
      white-space: nowrap;

      &::after {
        content: '';
        display: inline-block;
        width: 6px;
        height: 17px;
        margin-left: 2px;
        background: url("data:image/svg+xml,%3Csvg height='17' viewBox='0 0 6 17' width='6' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='m 1 6 l 3.5 3.5 c 0.15 0.15 0.15 0.4 0 0.55 l -3.5 3.5' fill='none' stroke='%23248bda' stroke-linecap='round' stroke-width='1.5'/%3E%3C/svg%3E") no-repeat center;
      }
    }
  }
}

// 响应式设计
@media (max-width: 768px) {
  .tm-command-edit-page {
    .tm-section {
      padding: 0 0 12px;
    }

    .tm-form-actions {
      padding: 0;
    }
  }
}

