// ============================================
// CommandDetail 页面特有样式
// ============================================
// 说明：此文件只包含 CommandDetail 页面特有的样式
// 全局样式和共享组件样式已在 src/index.tsx 中通过 @/styles/index.scss 导入
// ============================================

@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

// ============================================
// CommandDetail 页面特有样式覆盖
// ============================================
// 说明：此页面使用黑白配色方案，与全局的蓝色主题不同
// 使用 CSS 变量在页面作用域内覆盖全局颜色
// ============================================

// 页面容器 - 使用 CSS 变量覆盖颜色
.tm-commands-page {
  --page-accent-color: #000000;
  --page-text-primary: #000000;
  
  
  #aj_content {
    padding-top: 12px;
  }

  // 顶部介绍区域的特殊样式
  .tm-main-intro {
    .tm-main-intro-picture {
      margin: 12px auto 30px;
      background-color: var(--page-accent-color);
    }

    .tm-main-intro-header {
      color: var(--page-text-primary);
    }

    .tm-main-intro-text {
      a {
        color: var(--page-accent-color);
        white-space: nowrap;

        &::after {
          content: '';
          display: inline-block;
          width: 6px;
          height: 17px;
          margin-left: 2px;
          background: url("data:image/svg+xml,%3Csvg height='17' viewBox='0 0 6 17' width='6' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='m 1 6 l 3.5 3.5 c 0.15 0.15 0.15 0.4 0 0.55 l -3.5 3.5' fill='none' stroke='%23000' stroke-linecap='round' stroke-width='1.5'/%3E%3C/svg%3E") no-repeat center;
        }
      }
    }
  }

  // 区域头部的特殊样式
  .tm-section-header {
    position: relative;

    .tm-section-header-text {
      color: var(--page-text-primary);
    }

    .tm-section-action {
     
    &.edit-button {
      color: var(--accent-color);
      font-weight: 700;
      font-size: 14px;
      line-height: 28px;
      cursor: pointer;
      user-select: none;
    }}
  }
}

// CommandDetail 特有的行样式覆盖
.tm-commands-page {
  .tm-row {
    background: $bg-secondary;
    color: #000000;

    &:hover {
      background: #e8e8e8;
    }

    &.tm-row-add {
      font-weight: 600;
    }

    .tm-icon {
      width: 24px;
      height: 24px;
      background-color: #000000;
      mask-size: 24px;
      mask-position: center;
      mask-repeat: no-repeat;
      flex-shrink: 0;
      margin: 0 5px;
    }

    .tm-row-desc {
      color: #999999;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .tm-row-close {
      color: #f44336;
      cursor: pointer;
      margin-right: -12px;
    }
  }
}

// 编辑模式
.list-prevent-edit {
  .tm-icon,
  .tm-row-close {
    visibility: hidden;
    margin-left: -37px;
  }

  .js-sortable {
    padding: 9px 14px;
    padding-right: 24px;
  }
}

.js-edit-command-list:not(.list-prevent-edit) {
  .tm-row::after {
    display: none;
  }

  .tm-row {
    cursor: grab;

    &:active {
      cursor: grabbing;
    }
  }

  .tm-row-add {
    visibility: hidden;
    margin-top: -42px;
  }
}


// 响应式设计
@media (max-width: 768px) {
  .tm-commands-page {
    .tm-main-intro {
      padding: 16px 12px;
    }
  }
}

